
import { defineComponent, PropType, CSSProperties } from "vue";

interface Status {
  style: CSSProperties;
}

export default defineComponent({
  name: "overlay-component",
  props: {
    zIndex: {
      type: Object as PropType<CSSProperties["zIndex"]>,
    },
  },
  setup(props): Status {
    return {
      style: {
        zIndex: props.zIndex || 0,
      },
    };
  },
});
